import { connect } from "react-redux";
import Friends from "./Friends";
import { FollowUnfollowRequest, getFriends } from '../redux/friends-reducer';
import PaginationContainer from "./Pagination/PaginationContainer";
import React from "react";
import { getAllUsers, getButtonRequest, getCurrentPage, getFriendsList, getUsersForPage } from "../redux/friend-selectors";


class FriendsContainer extends React.Component {
    constructor(props){
        super(props)
    }

    componentDidMount() {
        this.props.getFriends(this.props.usersForPage, this.props.currentPage)
    }

    render() {
        return (
            <Friends 
                     friendsList= {this.props.friendsList}
                     FollowUnfollowRequest = {this.props.FollowUnfollowRequest}
                     PaginationContainer={this.props.PaginationContainer} 
                     buttonRequest = {this.props.buttonRequest}
                     onPageChanged={this.onPageChanged}
            />
        )
    }
}

const mapStateToProps = (state) => {
    return {
        friendsList: getFriendsList(state),
        allUsers: getAllUsers(state),
        usersForPage: getUsersForPage(state),
        currentPage: getCurrentPage(state),
        PaginationContainer: <PaginationContainer />,
        buttonRequest: getButtonRequest(state)
    }
}


export default connect(mapStateToProps, {FollowUnfollowRequest, getFriends})(FriendsContainer)